.first-child() when not (@rtl) {
  &:first-child{
    .border-left(1px solid @border3dRight);
    .border-right(1px solid @border3dLeft);
  }
}

.first-child() when (@rtl) {
  &:first-child{
    .border-left(1px solid @border3dRight);
  }
}

